<template>
  <div class="search_box">
    <template v-if="list.length">
      <div class="search_box_content">
        <div v-for="(item, index) in gs" :key="index" class="search_item" @click="toGame(item)">
          <load-img class="search-game-img" :width="imgSize.width" :height="imgSize.height" :item="imgFn(item)" />

          <div class="search_item_desp">
            <h4 class="search_title">{{ item.gameName }}</h4>
            <p>{{ item.factoryName }}</p>
          </div>
        </div>
      </div>
      <footer v-if="gs.length < list.length" class="pesquisa_footer text_center">
        <button class="btn--theme" color="t_btn_host_color" @click="goNext">
          {{ $t('General.General_8') }}
        </button>
      </footer>
    </template>
    <empty v-else></empty>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Empty from '@/components/ui/empty'
import loadImg from '@/components/ui/loadImg'
import variables from '@style/views/menu.index.modules.scss'

const { pesquisa_img_width, pesquisa_img_height, xs_pesquisa_img_width, xs_pesquisa_img_height } = variables

export default {
  components: {
    Empty,
    loadImg
  },
  props: {
    list: {
      type: Array,
      default: () => {}
    }
  },
  data() {
    return {
      size: 15,
      gs: []
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'langMoreInfo']),
    ...mapState('source', ['gameFactory']),
    imgSize() {
      if (this.isApp) {
        return {
          width: xs_pesquisa_img_width,
          height: xs_pesquisa_img_height
        }
      } else {
        return {
          width: pesquisa_img_width,
          height: pesquisa_img_height
        }
      }
    }
  },
  watch: {
    list: {
      deep: true,
      immediate: true,
      handler(e) {
        this.gs = e.slice(0, this.size)
      }
    }
  },
  methods: {
    getRateTips(item) {
      const info = this.langMoreInfo.find(f => f.name == 'currency_rate_withdraw_desc')
      let text = info ? info.value || '' : ''
      text = text.replaceAll('{game_name}', this.$route.query.gameName)
      text = text.replaceAll('{bw_rate}', item.egc_rate)
      return text
    },
    toGame(item) {
      if (item.show_rate_tips && item.show_rate_tips == 1) {
        let gameParams = { content: this.getRateTips(item), submitText: this.$t('UI.Resp_Dialog_Limit_Btn_Close') }
        this.$pop.mainten(gameParams).show()
        return
      }
      this.$emit('toGame', item)
    },
    goNext() {
      const list = this.list.slice(this.gs.length, this.gs.length + this.size)
      this.gs = this.gs.concat(list)
    },
    // 图片如果是has_child是否二级
    imgFn(item) {
      let url = ''
      if (item.has_child == 1) {
        url = this.isApp ? item.pic_phone_url : item.pic_pc_url
      } else {
        const { pic_url = '' } = item
        url = pic_url
      }
      return { url }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';
.search_box {
  width: 100%;
  overflow: auto;
}
.search_box_content {
  padding-bottom: $search_box_content_pb;
}

.search_item {
  display: flex;
  align-items: center;
  padding: $search_item_pa;
  margin-top: $search_item_mt;
}

.search-game-img {
  border-radius: $search_game_img_radius;
}

.search_item_desp {
  padding-left: $search_desp_pl;
  > h4 {
    margin: 0;
    font-size: $search_list_text1_font_size;
    font-weight: $search_list_text1_font_weight;
    color: $search_list_text1_color;
  }
  > p {
    font-size: $search_list_text2_font_size;
    font-weight: $search_list_text2_font_weight;
    margin-top: $search_list_text2_mt;
    color: $search_list_text2_color;
  }
}

.pesquisa_footer {
  margin: $pesquisa_footer_ma;
}
</style>
