<template>
  <v-container ref="boxIds" class="pesquisa_box">
    <div class="pesquisa_top">
      <site-map :title="$t('General.General_12')" @click-left="doBack" />
      <div class="pesquisa_search">
        <v-text-field
          v-model="keywords"
          class="search-field input_radius"
          background-color="t_input_light_bg"
          outlined
          :placeholder="$t('General.General_5')"
          :height="loadVariables('search_field_height')"
          hide-details
          single-line
          solo
          flat
          dense
        >
          <template slot="prepend-inner">
            <icon-font class="prepend-icon" name="icon-Icon_Search" :size="loadVariables('icon_search_size')" />
          </template>
        </v-text-field>
      </div>
    </div>
    <!-- tabs -->
    <div class="search_tabs">
      <!-- tabs 栏 -->
      <v-tabs
        v-model="tab"
        class="slide_icon_hide"
        center-active
        grow
        :color="loadVariables('search_tabs_color')"
        slider-color="t_primary_dark_color"
        :height="loadVariables('search_tabs_height')"
        @change="onTab"
      >
        <v-tab v-for="(tab, index) in list" :key="index" :ripple="false">
          {{ tab.type_name }}
        </v-tab>
      </v-tabs>
    </div>
    <!-- 底部搜索 -->
    <div id="scids" class="text_box">
      <template v-if="skeletonSearch">
        <space-ui sign="pesquisa_list" />
      </template>
      <template v-else>
        <search-list :list="newList" @toGame="toGame"></search-list>
      </template>
    </div>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import SearchList from './ui/search-list'
import gameAPI from '@/utils/game/gameAPI'
import spaceUi from '@/components/skeleton/space-ui.vue'
import SiteMap from '@/components/ui/site-map'
import variables from '@style/views/menu.index.modules.scss'

export default {
  name: 'GamePesquisa',
  components: {
    SearchList,
    spaceUi,
    SiteMap
  },
  mixins: [ContainerMixin],
  data() {
    return {
      // tabcolor: variables.
      list: [],
      todos: [],
      tab: 0,
      keywords: this.$root.$route.query?.keywords ?? '',
      newList: [],
      isFirstEnterPage: false,

      skeletonSearch: false // 搜索数据 骨架显示
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig']),
    ...mapState('source', ['gamelist', 'lobbylist'])
  },
  watch: {
    keywords(val) {
      this.search(val)
    }
  },
  created() {
    // if (this.lobbylist.length == 0) {
    // 	gameAPI.initGame(async (res = false) => {
    // 		await gameAPI.getTabs()
    // 		await gameAPI.getLobbyList()
    // 		if(res == true){
    // 			setTimeout(() =>{
    // 				gameAPI.favorateUp()
    // 			},100)
    // 		}
    // 	})
    // } else {
    // 	this.initGame().search(this.keywords)
    // }
    this.initGame().search(this.keywords)
  },
  mounted() {
    this.isFirstEnterPage = true
  },
  methods: {
    ...mapGetters('source', ['getExperienceAuth']),
    loadVariables(name) {
      return variables[name]
    },
    // 获取全部的游戏:新增一个全部
    initGame() {
      const list = this.lobbylist.map(v => v.game_list).reduce((a, b) => a.concat(b))
      this.todos = list
      this.list = [{ type_name: this.$root.$t('UI_ALL'), id: -1, game_list: list }, ...this.lobbylist]
      return this
    },
    // 搜索
    search(val, tab) {
      this.skeletonSearch = true
      let list = this.todos
      list = this.list[this.tab].game_list
      if (val) {
        this.$u.debounce(
          () => {
            this.newList = list.filter(item => item.gameName.toUpperCase().indexOf(val.trim().toUpperCase()) > -1)
            this.isFirstEnterPage = false
          },
          this.isFirstEnterPage ? 0 : 500
        )
      } else {
        this.newList = list
      }

      setTimeout(() => {
        this.skeletonSearch = false
      }, 500)
    },
    // 跳转游戏页面
    toGame(game = {}) {
      this.$store.dispatch('game/enterTheGame', { game, vm: this })
    },
    // 切换tab
    onTab(tab) {
      this.search(this.keywords, tab)
    },
    doBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.index.modules.scss';

.pesquisa_box {
  padding: $lobby_box_pa !important;
  .pesquisa_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: $featured_top_ma;
    .pesquisa_search {
      width: $field_search_width;
      .search-field {
        font-size: $search_field_font_size;
        .prepend-icon {
          margin-right: $search_field_prepend_icon_mr;
        }
      }
    }
  }
  .search_tabs {
    background: $search_tabs_background;
    box-shadow: none;
    border: 1px solid $search_tabs_border_color;
    padding: $search_tabs_pa;
    ::v-deep {
      .v-tab.v-tab--active {
        background: $pesquisa_tab_active_bg;
      }
    }
  }
  .text_box {
    width: 100%;
    overflow: hidden;
    background: $search_tabs_background;
    box-shadow: none;
    border: 1px solid $search_tabs_border_color;
    border-top: 0;
    margin-bottom: -1px;
    max-height: $search_text_box_max_height;
    overflow-y: auto;
  }
}

@include breakpoint-only-xs() {
  .pesquisa_box {
    .pesquisa_top {
      margin: $xs_featured_top_ma;
      .pesquisa_search {
        width: 100%;
        margin-top: $xs_field_search_mt;
      }
    }
    .search_tabs {
      padding: $xs_search_tabs_pa;
    }
    .text_box {
      max-height: none;
    }
  }
}
</style>
